import React from 'react';
import styles from './Navbar.module.scss';
import {NavLink} from 'react-router-dom';

class Navbar extends React.Component {
    state = {
        navbarClass : false,
    }
    componentDidMount = () => {
        setInterval(this.changeClass,100);
    }
    changeClass = () => {
        if(window.pageYOffset > 3){
            this.setState({navbarClass : true});
        } else {
            this.setState({navbarClass : false});
        }
    }
    render(){
        return(
            <>
                <ul className={this.state.navbarClass ? styles.nav2 : styles.nav}>
                    <li className={styles.logo}></li>
                    <li className={styles.listItem}><h2><NavLink className={styles.link} exact to="/jawbi/section1">Inside Central Unit</NavLink></h2></li>
                    <li className={styles.listItem}><h2><NavLink className={styles.link} exact to="/jawbi/section2">Output devices</NavLink></h2></li>
                    <li className={styles.listItem}><h2><NavLink className={styles.link} exact to="/jawbi/section3">Input devices</NavLink></h2></li>
                </ul>
            </>
        )
    }
}
export default Navbar;