import React from 'react';
import styles from './Section3.module.scss';
import array3 from './array3';
import AOS from 'aos';
import '../../aos.css';

class Section3 extends React.Component {

    componentDidMount = () => {
        AOS.init({duration : 750});
    }

    render(){
        return(
            <div className={styles.everything}> 
                <div className={styles.whatIs}>
                    <h1>Input Devices</h1>
                    <h2>Input devices enter data into the computer and control it.</h2>
                    <h2>For example :</h2>
                </div>
                {
                    array3.map(item => (
                        <div className={styles.sectionItem}>
                            <div className={styles.text}>
                                <h1 data-aos="fade-up">{item.title}</h1>
                                <p data-aos="fade-left" className={styles.p}>{item.text}</p>
                            </div>
                            <div className={styles.img}>
                                <img data-aos="fade-right" src={item.src} />
                            </div>
                        </div>
                    ))
                }
            </div>
        )
    }
}
export default Section3;