import React from 'react';
import styles from './Section2.module.scss';
import array2 from './array2';
import AOS from 'aos';
import '../../aos.css';

class Section2 extends React.Component {

    componentDidMount = () => {
        AOS.init({duration : 750});
    }

    render(){
        return(
            <div className={styles.everything}> 
                <div className={styles.whatIs}>
                    <h1>Output Devices</h1>
                    <h2>Output devices are deviceswhich we see or hear the effect of our work, e.g. a monitor, printer, plotter, speakers, headphones.</h2>
                    <h2>For example:</h2>
                </div>
                {
                    array2.map(item => (
                        <div className={styles.sectionItem}>
                            <div className={styles.text}>
                                <h1 data-aos="fade-up">{item.title}</h1>
                                <p data-aos="fade-left" className={styles.p}>{item.text}</p>
                            </div>
                            <div className={styles.img}>
                                <img data-aos="fade-right" src={item.src} />
                            </div>
                        </div>
                    ))
                }
            </div>
        )
    }
}
export default Section2;